import _tool from '@/util/tools.js';
export default {
	data() {
		return {
			TabCur: 0, 
			TabData: ['简介','目录'],
			bookData: {}, 
			listData: [], 
			order: '倒序', 
			BookID: null,
			addBtn: true, 
			newsID: null, 
			newsName: ''
		}
	},
	onLoad(option) {
		this.BookID = option.id;
		this.GetBookDetails(option.id);
		//是否已经加入书架
		_tool.GetBookListID(option.id).then((res) => {
			this.addBtn = !res;
		});
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		},
		GetBookDetails(id) {
			this.zaiApi({
				url:'dingdian/' + id,
			}).then((res) => {
				this.GetDetailsData(res)
			});
		},
		GetDetailsData(Book) {
			_tool.DetailsData(Book).then((res) => {
				this.bookData = res.data;
				this.listData = res.list;
				this.newsID = res.list[0].id;
				this.newsName = res.list[0].name;
			});
		},
		orderClick() {
			let order = this.order;
			this.order = order === '正序' ? '倒序' : '正序'
			//排序
			this.listData = this.listData.reverse();
		},
		AddClick() {
			let bookData = {
				id: this.BookID,
				news: this.newsID,
				page: 0,
				index: 1,
				name: this.newsName,
				title: this.bookData.name,
				img: this.bookData.img
			};
			_tool.AddBookListData(bookData).then((res) => {
				this.addBtn = true;
			});
		},
		readClick() {
			let bookData = {
				id: this.BookID,
				news: this.newsID,
				page: 0,
				index: 1,
				name: this.newsName,
				title: this.bookData.name,
				img: this.bookData.img
			};
			try {
			    uni.setStorageSync('BookReadData', bookData);
				uni.navigateTo({
					url: '/pages/bookshelf/read'
				})
			} catch (e) {
			    uni.showToast({
			        title: '数据异常',
			        icon: 'none'
			    });
			}
		},
		details(item,index) {
			let order = this.order;
			let readId = index + 1;
			if(order=='正序') {
				let listId = this.listData.length;
				readId = listId - index - 1;
			}
			let bookData = {
				id: this.BookID,
				news: item.id,
				page: 0,
				index: readId,
				name: item.name,
				title: this.bookData.name,
				img: this.bookData.img
			};
			try {
			    uni.setStorageSync('BookReadData', bookData);
				uni.navigateTo({
					url: '/pages/bookshelf/read'
				})
			} catch (e) {
			    uni.showToast({
			        title: '数据异常',
			        icon: 'none'
			    });
			}
		}
	}
}
